.loading-indicator {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 20px;
  border-radius: 5px;
  z-index: 1000;
}


.fc-daygrid-day {
  transition: all 0.3s ease;
  cursor: pointer;
}

/* Efecto hover para las celdas */
.fc-daygrid-day:hover {
  background-color: #f0fcff !important;
}

/* D√≠a actual */
.fc-day-today {
  background-color: #e6f7ff !important;
}

/* D√≠a seleccionado */
.fc-daygrid-day-selected {
  background-color: #d4e6ff !important;
  box-shadow: inset 0 0 0 1px #00000034 !important;
}

/* Eventos del calendario */
.fc-event {
  border-radius: 4px;
  padding: 2px 4px;
  font-size: 0.85em;
  margin-bottom: 2px;
}