@use "base/variables" as v;
@use "base/mixins" as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    overflow-x: hidden;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    position: relative;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    line-height: 1.8;
    color: v.$negro;
    overflow-x: hidden;
    max-height: 100vh;
    background-color: v.$crema-blanco;
    margin: 0;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    font-family: v.$fuente_titulo;
    text-transform: uppercase;
    font-weight: 100;
    letter-spacing: .1rem;
}

p {
    margin: 0;
    font-weight: 300;
}

button, a, .flotante, [role="button"] {
  -webkit-tap-highlight-color: transparent;
}