@use "base/variables" as v;
@use "base/mixins" as m;

.error-mensaje {
    width: 100%;
    max-height: 0;
    background-color: v.$rosa-bebe;
    font-size: 1.5rem;
    text-align: center;
    margin-bottom: 0;
    padding: 0;
    border-radius: 2rem;
    color: v.$blanco;
    opacity: 0;
    transform: translateY(-1rem);
    transition: all .3s ease;
}

.error-mensaje.visible {
    max-height: fit-content;
    transform: translateY(0);
    opacity: 1;
    margin-bottom: 2rem;
    padding: .5rem 0;
    transition: all .3s ease;
}

.error-global {
    margin: 0 auto 5rem auto;
    padding: 0 1rem;
    display: flex;
    align-items: center !important;
    justify-content: center !important;
    width: 90%;
    text-align: center;
}

.cargando-fechas, .sin-fechas {
    text-align: center;
    font-family: v.$fuente_titulo;
    color: v.$marron;
    text-transform: uppercase;
    font-weight: 700 !important;
}